.header {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    &__container {
        padding-top: 20px;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: flex-start;

        @include mq($small) {
            padding-top: 20px;
            padding-bottom: 20px;
            justify-content: space-between;
        }

        @include mq($medium) {

        }
    }

    &__logo {
        max-width: 118px;

        width: 100%;
        flex-shrink: 1;

        @include mq($medium) {
            max-width: 110px;

        }

        @include mq($medium) {
            max-width: 140px;
        }
    }

    &__nav {
        position: relative;
        margin-left: auto;
        @include mq($small) {
            left: 0;
            margin-left: 0;

        }
        @include mq($medium) {
            left: 22px;
        }
    }

    &__controls {
        display: none;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: nowrap;

        @include mq($small) {
            display: flex;
        }

        @include mq($medium) {

        }
    }

    &__lang {
        color: #777;
        font-family: $sfpro;
        @include font-size(12px);
        font-weight: normal;
        line-height: 1.5;
        letter-spacing: -0.06px;
    }

    &__contact {
        margin-left: 16px;
        @include mq($medium) {
            margin-left: 17px;

        }
    }

}

.header__nav-appearance {
    animation: fadeInRight .3s ease-in-out;
}

.header__nav-disappear {
    animation: fadeOutRight .3s ease-in-out;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translate3d(100%,0,0)
  }

  to {
    opacity: 1;
    transform: none
  }
}

@keyframes fadeOutRight {
  0% {
    opacity: 1
  }

  to {
    opacity: 0;
    transform: translate3d(100%,0,0)
  }
}
